// -------------------------------
// Import modern Sass modules
// -------------------------------
@use "sass:color";
@use "sass:math";

// -------------------------------
// Fonts & Variables
// -------------------------------
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&family=Montserrat:wght@400;600;700;900&display=swap');


$primary-font: 'Montserrat', sans-serif;
$secondary-font: 'Lato', sans-serif;


$primary-color: #0077ff;
$secondary-color: #333;
$background-color: #f8f9fa;
$card-bg-color: #fff;

// Color variables
$black-color: #000;
$dark-gray-color: #333;
$gray-border-color: #ccc;
$soft-blue-bg: #e9f7fe;
$light-teal-bg: #e0f7fa;
$utm-id-bg: #ffe0b2;
$utm-source-bg: #b3e5fc;
$utm-medium-bg: #c8e6c9;
$utm-campaign-bg: #f8bbd0;
$utm-term-bg: #d1c4e9;
$utm-content-bg: #fff9c4;

// Font sizes with clamp
$h1-font: clamp(3rem, 2.5385rem + 2.0513vw, 4rem);
$h2-font: clamp(2.4rem, 2.1231rem + 1.2308vw, 3rem);
$h3-font: clamp(2.1rem, 1.9154rem + 0.8205vw, 2.5rem);
$h4-font: clamp(1.8rem, 1.7077rem + 0.4103vw, 2rem);
$h5-font: clamp(1.6rem, 1.5308rem + 0.3077vw, 1.75rem);
$h6-font: clamp(1.4rem, 1.3538rem + 0.2051vw, 1.5rem);
$p-font: clamp(1rem, 1.4327rem + -1.9231vw, 0.0625rem);

// -------------------------------
// CSS Reset
// -------------------------------
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

// -------------------------------
// Body
// -------------------------------
body {
    font-family: $primary-font;
    font-size: 1rem;
    padding: 1.25rem;
    background: $background-color;
    color: $secondary-color;
}

// -------------------------------
// Headings
// -------------------------------
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    font-family: $primary-font;
    color: $primary-color;
    line-height: 1.25;
}

h1 {
    font-size: $h1-font;
    margin: 1rem 0;
    line-height: 1.25;
}

h2 {
    font-size: $h2-font;
    margin: 0.5rem 0;
    line-height: 1.3;
}

h3 {
    font-size: $h3-font;
    margin: 0.4rem 0;
    line-height: 1.35;
}

h4 {
    font-size: $h4-font;
    margin: 0.35rem 0;
    line-height: 1.4;
}

h5 {
    font-size: $h5-font;
    margin: 0.3rem 0;
    line-height: 1.45;
}

h6 {
    font-size: $h6-font;
    margin: 0.25rem 0;
    line-height: 1.5;
}

// -------------------------------
// Paragraphs
// -------------------------------
p {
    font-weight: 400;
    font-family: $secondary-font;
    color: $secondary-color;
    font-size: $p-font;
    line-height: 1.6;
    margin-bottom: 1rem;
}


// -------------------------------
// Containers
// -------------------------------
.container,
.container-fluid {
    max-width: 75rem;
    margin: 0 auto;
    background: $card-bg-color;
    padding: clamp(1.25rem, 2vw, 2rem);
    border-radius: 0.5rem;
    box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.08);
    margin-bottom: clamp(1rem, 2vw, 2rem);
}

.container-fluid {
    background: $light-teal-bg;
}


// -------------------------------
// Rows & Columns
// -------------------------------
.row {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
}

// Generate columns 1 through 12
@for $i from 1 through 12 {
    .col-#{$i} {
        flex: 0 0 math.percentage(math.div($i, 12));
        max-width: math.percentage(math.div($i, 12));
    }
}

// Optional overrides for UTM Builder layout
.col-6 {
    flex: 0 0 49%;
    max-width: 49%;
}

.col-12 {
    flex: 0 0 100%;
    max-width: 100%;
}

// Responsive stacking for small screens
@media (max-width: 600px) {
    @for $i from 1 through 12 {
        .col-#{$i} {
            flex: 0 0 100%;
            max-width: 100%;
        }
    }

    .button-group {
        flex-direction: column;
    }
}

// -------------------------------
// Labels & Inputs
// -------------------------------
label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.25rem;
    font-size: 1rem;
}

input {
    width: 100%;
    padding: 0.625rem;
    border: $black-color solid 0.0625rem;
    border-radius: 0.375rem;
    font-size: 1rem;
    margin-bottom: 0.5rem;

    &:focus-visible {
        outline: 0.125rem solid color.adjust($primary-color, $lightness: -20%);
        outline-offset: 0.125rem;
    }
}

// -------------------------------
// Buttons
// -------------------------------
button {
    padding: 0.625rem 1rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.375rem;
    border: $black-color solid 0.0625rem;
    background: $primary-color;
    color: $card-bg-color;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.2s ease-in-out, transform 0.1s ease-in-out;

    &:hover {
        background: color.adjust($primary-color, $lightness: -15%);
        transform: translateY(-0.0625rem);
    }

    &:active {
        transform: translateY(0);
    }

    &:focus-visible {
        outline: 0.125rem solid color.adjust($primary-color, $lightness: -20%);
        outline-offset: 0.125rem;
    }

    &.export-btn {
        background: color.adjust($primary-color, $lightness: -5%);

        &:hover {
            background: color.adjust($primary-color, $lightness: -25%);
        }
    }

    &.export-all-btn {
        background: color.adjust($primary-color, $lightness: 10%);
        color: $black-color;

        &:hover {
            background: color.adjust($primary-color, $lightness: -5%);
        }
    }
}

// -------------------------------
// Button Group
// -------------------------------
.button-group {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;
    margin-bottom: 1rem;

    button {
        flex: 1 1 auto;
        min-width: 120px;
        text-align: center;
    }
}

// -------------------------------
// Tooltips
// -------------------------------
.question-mark {
    cursor: pointer;
    color: $primary-color;
    font-weight: bold;
    margin-left: 0.25rem;
}

.tooltip-modal {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: $card-bg-color;
    border: 1px solid $gray-border-color;
    padding: 0.75rem;
    border-radius: 0.375rem;
    box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.2);
    z-index: 1000;
    font-size: 0.95rem;

    .close-tooltip {
        cursor: pointer;
        float: right;
        font-weight: bold;
    }
}

// -------------------------------
// Final URL Display
// -------------------------------
#finalUrl {
    padding: 0.625rem;
    background: $soft-blue-bg;
    border-radius: 0.375rem;
    word-break: break-all;
    font-size: 1rem;
    margin-top: 0.5rem;
    color: $dark-gray-color;
}

// -------------------------------
// UTM Cards
// -------------------------------
.utm-guide {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-top: 1rem;
}

.utm-card {
    font-family: $primary-font;
    color: $black-color;
    flex: 0 0 48%;
    padding: 1rem;
    font-size: clamp(0.9rem, 1vw + 0.8rem, 1rem);
    font-weight: 500;
    border-radius: 0.375rem;
    border: 1px solid $gray-border-color;
    box-shadow: 0 0.0625rem 0.25rem rgba(0, 0, 0, 0.08);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    text-align: left;
    cursor: pointer;
    transition: transform 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

    &::before {
        display: block;
        font-size: 1.5rem;
        margin-bottom: 0.25rem;
    }

    &:hover {
        transform: translateY(-0.125rem);
        box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.15);
    }

    &:focus-visible {
        outline: 0.125rem solid color.adjust($primary-color, $lightness: -20%);
        outline-offset: 0.125rem;
    }
}

.utm-card.utm_id {
    background: $utm-id-bg;
    &::before {
        content: "üÜî";
    }
}

.utm-card.utm_source {
    background: $utm-source-bg;
    &::before {
        content: "üåê";
    }
}

.utm-card.utm_medium {
    background: $utm-medium-bg;
    &::before {
        content: "üì£";
    }
}

.utm-card.utm_campaign {
    background: $utm-campaign-bg;
    &::before {
        content: "üè∑Ô∏è";
    }
}

.utm-card.utm_term {
    background: $utm-term-bg;
    &::before {
        content: "üîë";
    }
}

.utm-card.utm_content {
    background: $utm-content-bg;
    &::before {
        content: "üìÑ";
    }
}

// -------------------------------
// Responsive
// -------------------------------
@media (max-width: 600px) {

    .utm-card,
    .col-6,
    .col-12 {
        flex: 0 0 100%;
    }

    .button-group {
        flex-direction: column;
    }
}

// -------------------------------
// Header Centering
// -------------------------------
.header-container {
    text-align: center;
    margin-bottom: 1.5rem;

    h1 {
        margin: 0;
    }
}